.Singup {
  width: 50vw;
  height: 50vw;
  margin: 4rem 0;
  background-color: white;
  border-radius: 10px;
}
form {
  width: 100%;
  height: 100%;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}

input {
  padding: 10px;
  border-radius: 5px;
  border: 2px solid #eee;
}

label {
  margin-left: 10px;
}

div {
  display: flex;
  align-items: center;
  justify-content: center;
}

button {
  padding: 10px 20px;
  border-radius: 5px;
  outline: none;
  border: none;
  background-color: purple;
  color: #fff;
}
